export function debounceByRaf(cb) {
    let timer = null;
    const debounce = function (...args) {
        timer && window.cancelAnimationFrame(timer);
        timer = window.requestAnimationFrame(() => {
            cb(...args);
            timer = null;
        });
    };
    debounce.cancel = () => {
        window.cancelAnimationFrame(timer);
        timer = null;
    };
    return debounce;
}
